<template>
  <div class="shoppingmall">
    <div class="header">
      <div class="logo">
        <img src="../assets/shoppingmall-logo.png" alt="" />
        <div class="text">
          <p>蘑菇街 总有高手帮你挑</p>
          <div class="icon">
            <img src="../assets/shoppingmall-start.png" alt="" />
            <img src="../assets/shoppingmall-start.png" alt="" />
            <img src="../assets/shoppingmall-start.png" alt="" />
            <img src="../assets/shoppingmall-start.png" alt="" />
            <img src="../assets/shoppingmall-start.png" alt="" />
            <span>超过2亿用户已下载</span>
          </div>
        </div>
      </div>
      <div class="btn">前往查看</div>
    </div>
    <div class="search">
      <div class="im-talk" @click="$router.push('/classify')"></div>
      <div class="input" @click="showSearchSuggest = true">
        <img src="../assets/home-search.png" alt="" />
        <input type="text" placeholder="关键词" />
      </div>
      <div class="cart"></div>
    </div>
    <ul class="imgblock">
      <li v-for="s in shoppingmallnav" :key="s._system_record_entry_id">
        <img v-lazy="s?.backgroundImage" />
        <p>{{ s.title }}</p>
        <div class="timestamp" v-if="s.timestamp">
          <van-count-down :time="time">
            <template #default="timeData">
              <span class="block">{{
                String(timeData.hours).padStart(2, "0")
              }}</span>
              <span class="colon">:</span>
              <span class="block">{{
                String(timeData.minutes).padStart(2, "0")
              }}</span>
              <span class="colon">:</span>
              <span class="block">{{
                String(timeData.seconds).padStart(2, "0")
              }}</span>
            </template>
          </van-count-down>
        </div>
      </li>
    </ul>
    <img src="../assets/shoppingmall-like.webp" alt="" />
    <ul class="like">
      <li
        v-for="item in shoppingmallLike"
        :key="item.iid"
        @click="$router.push({ path: '/commodity', query: { id: item.iid } })"
      >
        <img :src="item?.img" alt="" />
        <div class="msg">
          <p>{{ item.title }}</p>
          <div class="num">
            <span class="fl">&yen;{{ item.price }}</span>
            <span>{{ item.cfav }} </span>
          </div>
          <div class="btn">立即购买</div>
        </div>
      </li>
    </ul>

    <SearchSuggest
      v-if="showSearchSuggest"
      @exit-searchsuggest="showSearchSuggest = $event"
      @noshow-searchsuggest="showSearchSuggest = $event"
    />
  </div>
</template>

<script>
import { mapState } from "vuex";
import SearchSuggest from "@/components/SearchSuggest.vue";
export default {
  components: {
    SearchSuggest,
  },
  data() {
    return {
      showSearchSuggest: false,
    };
  },
  created() {
    this.$store.dispatch("getShoppingmallnav");
    this.$store.dispatch("getShoppingmallLike", 1);
  },
  computed: {
    ...mapState(["shoppingmallnav", "shoppingmallLike"]),
    time: function () {
      return this.shoppingmallnav[0].timestamp;
    },
  },
};
</script>

